.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

html, body {
    height: 100%;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}


.introContainer::after {
    content: "";
    background: url('bg.jpg');
    position: absolute;
    background-size: cover;
    opacity: 0.9;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.experienceBar {
    border-right: 1px solid lightgrey;
}

.experienceBarNotch {
    border: 1px solid black;
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background: #60a2bba3;
}

.content {
    border: 1px solid lightgrey;
    background: #60a2bba3;
    border-radius: 4px;
    cursor: pointer;
}

.content:hover {
    box-shadow: 0px 1px 6px 1px lightgrey;
}

.content h6 {
    top: 4px;
    position: relative;
}

.bar2, .bar4 {
    right: 5px !important;
    position: relative;
}

.bar1, .bar3 {
    left: 5px;
    position: relative;
}

.aboutContainer .list-group-item:hover {
    background: #cde8ff;
}

.animateName {
    animation: showName 2s infinite;
}

@keyframes showName {
    0% {
        opacity: 0;
    }
    50% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}